// root actions

//import state from './state.js'

import {asyGetTableItemsByRoute} from '../util.js'

export default {
	//async 函数返回的都是Promise对象
	async asyChangeTable({dispatch},opt){
		return await dispatch('asyGetTableItems',opt)
	},
	//async 函数返回的都是Promise对象
	async asyGetTableItems({commit,state},opt){
		let table=state['table']
		let fetchOption=state['fetchOption']
		let activeNav=state['activeNav']
		let navName=opt.hasOwnProperty('activeNav')?opt['activeNav']['name']:''
		
		let setActiveNav=name=>{
			let navArr=state['htmlItems']['nav']
			let navObj={}
			
			for(let obj of navArr){
				if(obj['name']==name){
					navObj=obj
					break
				}
			}
			return navObj
		}
		let result=false
		
		if(opt.hasOwnProperty('table')){
			table=Object.assign({},state['table'],opt['table'])
		}
		
		if(opt.hasOwnProperty('fetchOption')){
			fetchOption = Object.assign({},state['fetchOption'],opt['fetchOption'])
		}
		
		if(navName.length){
			activeNav=setActiveNav(navName)
		}
		
		if(fetchOption.routeName!==state.fetchOption.routeName){
			table['isBusy']=true
			commit('changeTable',table)
			
			table['items']=await asyGetTableItemsByRoute(fetchOption,table.fieldLang)
			table['isBusy']=false
			//只能通过commit mutations中的函数才能修改state中的数据
			commit('changeTable',table)
			commit('changeFetchOption',fetchOption)
			commit('changeActiveNav',activeNav)
			result=true
		}
		
		/* console.log(activeNav)
		console.log(table)
		console.log(fetchOption) */
		return result
	}
	
}